import { onMounted, onUnmounted, ref } from 'vue';
import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';
import Stats from 'stats.js'
import { Vector3 } from 'three';

export default function createScene(sceneRef: any, characterPosition: Vector3, cameraOffset: Vector3) {
    const scene: THREE.Scene = new THREE.Scene();
    const renderer: THREE.WebGLRenderer = new THREE.WebGLRenderer({ antialias: true });
    const camera: THREE.PerspectiveCamera = new THREE.PerspectiveCamera();
    const animation = ref();
    const animateFunctions: Array<any> = [];
    const controls: OrbitControls = new OrbitControls(camera, renderer.domElement);

    const onWindowResize = () => {
        camera.aspect = window.innerWidth / window.innerHeight;
        camera.updateProjectionMatrix();
        renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));
        renderer.setSize(window.innerWidth, window.innerHeight);
    }

    onMounted( () => {
        camera.fov = 60;
        camera.aspect = sceneRef.value ? sceneRef.value.clientWidth / sceneRef.value.clientHeight : window.innerWidth / window.innerHeight;
        camera.near = 1;
        camera.far = 300;
        camera.position.set(characterPosition.x + cameraOffset.x, characterPosition.y + cameraOffset.y, characterPosition.z + cameraOffset.z);
        camera.updateProjectionMatrix();
        renderer.setSize(sceneRef.value.clientWidth, sceneRef.value.clientHeight);
        renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));
        sceneRef.value.appendChild(renderer.domElement);
        scene.background = new THREE.Color( 0xcce0ff );
        scene.fog = new THREE.Fog( 0xcce0ff, 100, 300 );

        // zmiana rozmiaru sceny
        window.addEventListener('resize', onWindowResize);

        // Panel statystyk
        const stats = new Stats();
        stats.showPanel(0);
        document.body.appendChild( stats.dom );

        // Sterowanie kamera za pomoca myszy
        controls.enableDamping = true;
        controls.dampingFactor = 0.05;
        controls.screenSpacePanning = false;
        controls.minDistance = 10;
        controls.maxDistance = 30;
        controls.maxPolarAngle = 75* Math.PI/180
        controls.enablePan = false;
        // controls.enableRotate = false;
        controls.enableZoom = false;
        controls.target = characterPosition

        // Funkcja renderująca
        const render = () => {
            renderer.render(scene, camera);
        };

        // Główna pętla animacji
        const animate = () => {
            stats.begin();

            requestAnimationFrame(animate);
            controls.update();

            animateFunctions.forEach(func => {
                func();
            });

            render();

            stats.end();
        };

        animate();
    });

    onUnmounted(() => {
        window.removeEventListener('resize', onWindowResize);
    })

    return {
        scene,
        renderer,
        camera,
        animateFunctions,
        animation,
        controls
    };
}