import { onMounted, Ref } from 'vue';
import * as THREE from 'three';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';
import { AnimationAction, Object3D, Vector3 } from 'three';
import anime from 'animejs';

export default function addCharacter(
    loader: GLTFLoader,
    scene: THREE.Scene,
    objects: any,
    animations: any,
    mixers: any,
    pressedKeys: any,
    testAllKeys: any,
    testAnyKeys: any,
    gamepads: Ref<(Gamepad | null)[]>,
    testAllButtons: any,
    testAnyButton: any,
    testAxis: any,
    characterPath: string,
    controls: OrbitControls,
    camera: THREE.PerspectiveCamera,
    position: Vector3,
    cameraOffset: Vector3,
    animation: Ref
) {

    const raycaster = new THREE.Raycaster();
    const clock = new THREE.Clock();
    let pauseOthersAnimations = false;

    let currentAnimation: AnimationAction | null = null;
    let oneLoopTrigger = 0

    const playAnimation = (objectMixer: any, objectAnimations: any, animationName: any, acceleration = 1, loop = true) => {
        const clip = objectAnimations.find((el: any) => el.name === animationName);
        const animation = objectMixer.clipAction(clip);
        animation.setEffectiveTimeScale(acceleration);

        if(!loop && animation.time == animation.getClip().duration) {
            animation.reset()
        }

        if (!loop && animation != currentAnimation) {
            pauseOthersAnimations = true;
            animation.setLoop(THREE.LoopOnce, 1);
            animation.setEffectiveWeight(1).reset()
            currentAnimation?.fadeOut(getFadeTime(currentAnimation, animation))
            currentAnimation = animation

            // animation.value = anime({
            //     targets: objects['Robot'].position,
            //     easing: 'linear',
            //     direction: 'alternate',
            //     duration: animation.getClip().duration*1000 / 2,
            //     update: () => {
            //         const distance = raycaster.intersectObject(objects['floor'], true)[0].distance - 4
            //         console.log(objects['Robot'].position.y)
            //         console.log('distance', distance)
            //     },
            //     complete: () => {
            //         animation.value = null;
            //     },
            //     y: objects['Robot'].position.y + 5,
            // });
        }

        if (pauseOthersAnimations) {
            if(currentAnimation && currentAnimation.time/currentAnimation.getClip().duration >= 0.75) {
                pauseOthersAnimations = false
                animation.fadeIn(getFadeTime(animation, currentAnimation)).reset()
                currentAnimation = animation
            }
        } else {
            animation.setEffectiveWeight(1)
            currentAnimation = animation
        }
    }

    const getFadeTime = (startAnimation: AnimationAction, endAnimation: AnimationAction) => {
        const startAnimationTime = startAnimation.getClip().duration / startAnimation.getEffectiveTimeScale()
        const endAnimationTime = endAnimation.getClip().duration / endAnimation.getEffectiveTimeScale()

        if (endAnimationTime * 0.25 > startAnimationTime) {
            return startAnimationTime
        } else {
            return endAnimationTime * 0.25
        }
    }

    const stopAnimation = (objectMixer: any, objectAnimations: any, animationName: any) => {
        const clip = objectAnimations.find((el: any) => el.name === animationName);
        const animation = objectMixer.clipAction(clip);
        animation.setEffectiveWeight(0)
    }

    const updateCameraPosition = () => {
        // animation.value = anime({
        //     targets: camera.position,
        //     easing: 'easeOutCirc',
        //     duration: 1000,
        //     complete: () => {
        //         animation.value = null;
        //     },
        //     x: objects['Robot'].position.x + cameraOffset.x,
        //     y: objects['Robot'].position.y + cameraOffset.y,
        //     z: objects['Robot'].position.z + cameraOffset.z
        // });

        animation.value = anime({
            targets: controls.target,
            easing: 'easeOutCirc',
            duration: 2000,
            complete: () => {
                animation.value = null;
            },
            x: objects['Robot'].position.x,
            y: objects['Robot'].position.y,
            z: objects['Robot'].position.z,
        });
    }

    const getCoordsFromAngle = (angle: number) => {
        const z = Math.cos(angle * Math.PI / 180)
        const x = Math.sin(angle * Math.PI / 180)
        return {z, x}
    }

    const checkKeysState = () => {
        const robotPosition = objects['Robot'].position.clone()
        const robotRotation = objects['Robot'].rotation.y * 180 / Math.PI
        const skeletonExtremes = getSkeletonExtremes(objects['Robot'])
        const robotHeight = skeletonExtremes.max.y-skeletonExtremes.min.y // wysokość postaci
        const raycasterPosition = robotPosition.clone()
        raycasterPosition.y += robotHeight;
        let speed = 0.18;
        const horizontalOffset = 1; // powinien wynosić promień poziomy postaci
        const distanceLimitUp = 1.2; // limit wielkości na jaką może wejść (bez skoku)
        const distanceLimitDown = 5; // limit wielkości z jakiej może zejść
        let angle = robotRotation;
        const move = new THREE.Vector3();
        const oneLoop = currentAnimation?.loop == 2200
        const axis = testAxis()
        const cameraAngle = controls.getAzimuthalAngle() * 180/Math.PI

        // const robotPositionHelper = new THREE.Mesh(new THREE.SphereGeometry( 0.1, 8, 8 ), new THREE.MeshBasicMaterial( {color: 0xff0000} ));
        // robotPositionHelper.position.set(robotPosition.x, robotPosition.y, robotPosition.z)
        // scene.add(robotPositionHelper)
        // setTimeout(() => {
        //     scene.remove(robotPositionHelper)
        // }, 300);

        if ((testAllKeys(['ShiftLeft']) || testAllButtons([7])) && !oneLoop) {
            speed *= 2;
        }

        if (testAllKeys(['KeyW'], ['KeyA', 'KeyS', 'KeyD']) || testAllButtons([12], [13,14,15])) {
            angle = 180 + cameraAngle;
        } else if (testAllKeys(['KeyA'], ['KeyW', 'KeyS', 'KeyD']) || testAllButtons([14], [12,13,15])) {
            angle = -90 + cameraAngle;
        } else if (testAllKeys(['KeyS'], ['KeyW', 'KeyA', 'KeyD']) || testAllButtons([13], [12,14,15])) {
            angle = 0 + cameraAngle;
        } else if (testAllKeys(['KeyD'], ['KeyW', 'KeyA', 'KeyS']) || testAllButtons([15], [12,13,14])) {
            angle = 90 + cameraAngle;
        } else if (testAllKeys(['KeyW', 'KeyA']) || testAllButtons([12, 14])) {
            angle = -135 + cameraAngle;
        } else if (testAllKeys(['KeyW', 'KeyD']) || testAllButtons([12, 15])) {
            angle = 135 + cameraAngle;
        } else if (testAllKeys(['KeyS', 'KeyA']) || testAllButtons([13, 14])) {
            angle = -45 + cameraAngle;
        } else if (testAllKeys(['KeyS', 'KeyD']) || testAllButtons([13, 15])) {
            angle = 45 + cameraAngle;
        }

        if (testAnyKeys(['KeyW', 'KeyA', 'KeyS', 'KeyD']) || testAnyButton([12,13,14,15]) || testAxis()) {
            move.x = getCoordsFromAngle(angle).x * speed;
            move.z = getCoordsFromAngle(angle).z * speed;
            raycasterPosition.x += getCoordsFromAngle(angle).x * speed + horizontalOffset;
            raycasterPosition.z += getCoordsFromAngle(angle).z * speed + horizontalOffset;
        }

        if(axis) {
            angle = axis.angle + cameraAngle
            move.x = axis.x * speed
            move.z = axis.z * speed
            raycasterPosition.x += axis.x * speed + (axis.x > 0? horizontalOffset : -horizontalOffset);
            raycasterPosition.z += axis.z * speed + (axis.z > 0? horizontalOffset : -horizontalOffset);
        }

        updateCameraPosition()
        const rotationRange = Math.abs(robotRotation - angle)
        if(oneLoop && rotationRange > 45 && rotationRange < 315) {
            angle = robotRotation
            move.x = 0
            move.z = 0
        }

        // Wykrywanie kolizji
        raycaster.set(raycasterPosition, new THREE.Vector3(0, -1, 0));
        const intersects = raycaster.intersectObject(objects['floor'], true);

        // Pokazuje promienie raycastera
        // const raycasterHelper = new THREE.ArrowHelper(raycaster.ray.direction, raycaster.ray.origin, robotHeight, 0xff00ff)
        // scene.add(raycasterHelper);
        // setTimeout(() => {scene.remove(raycasterHelper)}, 3000);

        // Odległość od raycastera do najniższej części robota
        const rayToRobotDistance = Math.abs(skeletonExtremes.min.y - robotPosition.y)
        // const rayToRobotDistanceHelper = new THREE.ArrowHelper(new Vector3(0, 1, 0), robotPosition, rayToRobotDistance, 0xff0000, 0, 0)
        // rayToRobotDistanceHelper.renderOrder = 9999
        // scene.add(rayToRobotDistanceHelper)
        // setTimeout(() => {
        //     scene.remove(rayToRobotDistanceHelper)
        // }, 3000);

        // Odległość od kolizji do pozycji Robota
        const collistionToRobotTop = intersects[0] ? intersects[0].distance - robotHeight : 0

        // Sprawdza czy poruszamy sie w dół czy do góry
        const goUp = collistionToRobotTop >= 0 ? false : true

        // if(intersects[0]) {
        //     const collistionToRobotTopPosition = intersects[0].point.clone()
        //     collistionToRobotTopPosition.x += 0.01
        //     collistionToRobotTopPosition.z += 0.01
        //     const collistionToRobotTopHelper = new THREE.ArrowHelper(new Vector3(0, goUp? -1 : 1, 0), collistionToRobotTopPosition, Math.abs(collistionToRobotTop), 0xffff00, 0, 0)
        //     collistionToRobotTopHelper.renderOrder = 99999
        //     scene.add(collistionToRobotTopHelper)
        //     setTimeout(() => {
        //         scene.remove(collistionToRobotTopHelper)
        //     }, 3000);
        // }

        if (intersects[0] && Math.abs(collistionToRobotTop) < distanceLimitUp + rayToRobotDistance && goUp) {
            if(oneLoop) {
                if(rayToRobotDistance >= Math.abs(collistionToRobotTop)) {
                    move.y = 0
                } else {
                    if (oneLoopTrigger < Math.abs(collistionToRobotTop + rayToRobotDistance)) {
                        move.y = Math.abs(collistionToRobotTop + rayToRobotDistance)
                        if(currentAnimation) currentAnimation.warp(currentAnimation.timeScale, currentAnimation.timeScale*4, currentAnimation.getClip().duration-currentAnimation.time)
                    } else {
                        move.y = 0
                    }
                }

                oneLoopTrigger = Math.abs(collistionToRobotTop + rayToRobotDistance)
                return { move, angle };
            } else {
                oneLoopTrigger = 0
                move.y = -intersects[0].distance + robotHeight;
                return { move, angle };
            }
        } else if(intersects[0] && collistionToRobotTop < distanceLimitDown && !goUp) {
            move.y = -intersects[0].distance + robotHeight;
            return { move, angle };
        } else {
            move.x = 0;
            move.y = 0;
            move.z = 0;
            return { move, angle};
        }
    }

    const robotRunning = () => {
        if (mixers['Robot']) {
            if (testAnyKeys(['KeyW', 'KeyA', 'KeyS', 'KeyD']) || testAnyButton([12,13,14,15]) || testAxis()) {
                let acceleration = 1;
                if (testAllKeys(['ShiftLeft']) || testAllButtons([7])) {
                    acceleration = 2;
                }
                playAnimation(mixers['Robot'], animations['Robot'], 'Robot_Running', acceleration);
            } else {
                stopAnimation(mixers['Robot'], animations['Robot'], 'Robot_Running');
            }

            if (testAnyKeys(['Space']) || testAllButtons([0])) {
                if(!pauseOthersAnimations) {
                    playAnimation(mixers['Robot'], animations['Robot'], 'Robot_Jump', 0.6, false);
                }
            }
        }
    }

    const toAnimate = () => {
        for (const model in mixers) {
            mixers[model].update(clock.getDelta());
        }

        // Jeżeli którykolwiek klawisz jest wciśnięty
        if (Object.values(pressedKeys).includes(true) || pauseOthersAnimations || gamepads.value[0]) {
            const keysStateResult = checkKeysState();
            objects['Robot'].position.add(keysStateResult.move);
            objects['Robot'].rotation.y = keysStateResult.angle * (Math.PI / 180);
        }
    }

    const getSkeletonExtremes = (object: THREE.Object3D) => {
        const skeleton = new THREE.SkeletonHelper(object)
        const bone_min = {x:Infinity,y:Infinity,z:Infinity};
        const bone_max = {x:-Infinity,y:-Infinity,z:-Infinity};

        for(let b=0; b < skeleton.bones.length; b++){
            const child = skeleton.bones[b];
            const position = new THREE.Vector3();
            child.getWorldPosition(position);

            if(position.x < bone_min.x) bone_min.x = position.x
            if(position.y < bone_min.y) bone_min.y = position.y
            if(position.z < bone_min.z) bone_min.z = position.z

            if(position.x > bone_max.x) bone_max.x = position.x
            if(position.y > bone_max.y) bone_max.y = position.y
            if(position.z > bone_max.z) bone_max.z = position.z
        }

        return {min: bone_min, max: bone_max}
    }

    onMounted( () => {
        loader.load(
            characterPath,
            gltf => {
                const model = gltf.scene;
                model.userData.name = 'Robot';
                model.position.set(position.x, position.y, position.z);
                scene.add(model);

                //Zapisz dane obiektu
                objects[model.userData.name] = model; // Obiekt
                animations[model.userData.name] = gltf.animations; // Animacja
                mixers[model.userData.name] = new THREE.AnimationMixer(model); // Mikser animacji
                mixers[model.userData.name].addEventListener('finished', function(e: any) {
                    pauseOthersAnimations = false;
                    robotRunning()
                    //console.log('animacja '+ e.action._clip.name +' skonczona');
                });
                animations[model.userData.name].forEach((animation: THREE.AnimationClip) => {
                    mixers[model.userData.name].clipAction(animation).setEffectiveWeight(0).play()
                });
            },
            undefined,
            function(error) {
                console.error(error);
            },
        );
    });

    return {
        robotRunning,
        toAnimate
    }
}